import '../public/css/result.css'
import '../public/css/btn.scss'
import '../public/iconfont/iconfont.css'
import '../public/css/flags.css'
import '../public/css/hl-vue-ui-base.css'
import '../public/css/animation.scss'
import '../public/js/hl-vue-ui-base.js'

import Input from './hl-input'
import Statistic from './hl-statistic'
import ImgCard from './hl-img-card'
import Collapse from './hl-collapse'
import ElDemoBlock from './hl-el-demo-block'
import TableSimple from './hl-table-simple'
import BackTop from './hl-backtop'
import Button from './hl-button'
import Card from './hl-card'
import FooterToolbar from './hl-footertoolbar'
import Drawer from './hl-drawer'
import Loading from './hl-loading'
import UploadImg from './hl-upload-img'
import PreviewImg from './hl-preview-img'
import Modal from './hl-modal'
import Alert from './hl-alert'
import Anchor from './hl-anchor'
import AnchorLink from './hl-anchor-link'
import Divider from './hl-divider'
import MenuSimple from './hl-menu-simple'
import PulldownRefresh from './hl-pulldown-refresh'
import LoadingPage from './hl-loading-page'
import UrlParam from './hl-url-param'
import ModelMask from './hl-model-mask'
import DateRangePicker from './hl-date-range-picker'
import CountryPicker from './hl-country-picker'
import HlZoomimg from './hl-zoom-img'
import WidgetModal from './hl-widget-modal'

import VueCropper from './vue-cropper'
import VueDragResize from './vue-drag-resize'

const components = [
    Input,
    Statistic,
    ImgCard,
    Collapse,
    ElDemoBlock,
    TableSimple,
    BackTop,
    Button,
    FooterToolbar,
    Drawer,
    Loading,
    UploadImg,
    PreviewImg,
    Modal,
    Alert,
    Anchor,
    AnchorLink,
    Divider,
    Card,
    MenuSimple,
    PulldownRefresh,
    LoadingPage,
    UrlParam,
    ModelMask,
    DateRangePicker,
    CountryPicker,
    HlZoomimg,
    WidgetModal,

    VueDragResize,
    VueCropper
]

// 定义 install 方法，接收 Vue 作为参数。如果使用 use 注册插件，则所有的组件都将被注册
const install = function (Vue) {
    // 判断是否安装
    if (install.installed) return
    // 遍历注册全局组件
    components.map(component => Vue.component(component.name, component))
}

// 判断是否是直接引入文件
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}

export default {
    // 导出的对象必须具有 install，才能被 Vue.use() 方法安装
    install,
    
    // 以下是具体的组件列表
    Input,
    Statistic,
    ImgCard,
    Collapse,
    ElDemoBlock,
    TableSimple,
    BackTop,
    Button,
    FooterToolbar,
    Drawer,
    Loading,
    UploadImg,
    PreviewImg,
    Modal,
    Alert,
    Anchor,
    AnchorLink,
    Divider,
    Card,
    MenuSimple,
    UrlParam,
    PulldownRefresh,
    LoadingPage,
    ModelMask,
    DateRangePicker,
    CountryPicker,
    HlZoomimg,
    WidgetModal,

    VueDragResize,
    VueCropper
}