<template>
  <div class="content-box">
    <h2 class="demo-title">Alert 警告</h2>
    <p class="demo-introduction">用于页面中展示重要的提醒信息。</p>

    <div class="demo-tip">
      <p>
        Alert组件提供四种主题，由type属性指定，默认值为info！Alert组件为非浮层组件，不会自动消失！
      </p>
    </div>
    <hl-anchor>
      <hl-anchor-link href="alert-simple" title="基础用法" />
      <hl-anchor-link href="alert-closable" title="可关闭" />
      <hl-anchor-link href="alert-center" title="文字居中" />
      <hl-anchor-link href="api" title="Alert API" />
      <hl-anchor-link href="events" title="Alert Events" />
    </hl-anchor>
    <!-- 基础用法DEMO -->
    <hl-el-demo-block :height="147" :title="'基础用法'" id="alert-simple">
      <template v-slot:demo>
        <hl-alert type="success" title="成功的提示文案！"></hl-alert>
        <hl-alert type="info" title="消息提示的文案！"></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！"></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！"></hl-alert>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(simple.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- 可关闭的DEMO -->
    <hl-el-demo-block :height="147" :title="'可关闭'" id="alert-closable">
      <template v-slot:demo>
        <hl-alert type="success" title="成功的提示文案！" closable></hl-alert>
        <hl-alert type="info" title="消息提示的文案！" closable></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！" closable></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！" closable></hl-alert>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(closable.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- 可自定义的DEMO -->
    <hl-el-demo-block
      :height="147"
      :title="'自定义关闭按钮'"
      id="alert-closeText"
    >
      <template v-slot:demo>
        <hl-alert
          type="success"
          title="成功的提示文案！"
          closeText="我知道了"
        ></hl-alert>
        <hl-alert
          type="info"
          title="消息提示的文案！"
          closeText="点击关闭"
        ></hl-alert>
        <hl-alert
          type="warning"
          title="警告提示的文案！"
          closable
          iconClass="hl hl-star-fill"
        ></hl-alert>
        <hl-alert
          type="danger"
          title="危险提示的文案！"
          closable
          iconClass="hl hl-github-fill"
        ></hl-alert>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(closeText.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- 文字居中展示 -->
    <hl-el-demo-block :height="168" :title="'文字居中'" id="alert-center">
      <template v-slot:demo>
        <hl-alert
          type="success"
          title="成功的提示文案！"
          closable
          center
        ></hl-alert>
        <hl-alert
          type="info"
          title="消息提示的文案！"
          closable
          center
        ></hl-alert>
        <hl-alert
          type="warning"
          title="警告提示的文案！"
          closable
          center
        ></hl-alert>
        <hl-alert
          type="danger"
          title="危险提示的文案！"
          closable
          center
        ></hl-alert>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(customize.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- 展示图标 -->
    <hl-el-demo-block :height="168" :title="'展示图标'" id="alert-showIcon">
      <template v-slot:demo>
        <hl-alert
          type="success"
          title="成功的提示文案！"
          closable
          showIcon
        ></hl-alert>
        <hl-alert
          type="info"
          title="消息提示的文案！"
          closable
          showIcon
        ></hl-alert>
        <hl-alert
          type="warning"
          title="警告提示的文案！"
          closable
          center
          showIcon
        ></hl-alert>
        <hl-alert
          type="danger"
          title="危险提示的文案！"
          center
          showIcon
        ></hl-alert>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(showIcon.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- 不同主题 -->
    <hl-el-demo-block :height="168" :title="'不同主题'" id="alert-effect">
      <template v-slot:demo>
        <hl-alert
          type="success"
          title="成功的提示文案！"
          closable
          effect="dark"
        ></hl-alert>
        <hl-alert
          type="info"
          title="消息提示的文案！"
          closable
          effect="dark"
        ></hl-alert>
        <hl-alert
          type="warning"
          title="警告提示的文案！"
          closable
          effect="dark"
        ></hl-alert>
        <hl-alert
          type="danger"
          title="危险提示的文案！"
          closable
          effect="dark"
        ></hl-alert>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(effect.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">Alert API</h3>
      <hl-table-simple :data="alertApi" :type="'demo'" />
    </div>

    <!-- events -->
    <div id="events">
      <h3 class="demo-table-title">Alert Events</h3>
      <hl-table-simple :data="EventsApi" :type="'events'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'hl-alert-demo',
  data () {
    return {
      simple: {
        code: {
          html: `
          <hl-alert type="success" title="成功的提示文案！"></hl-alert>
        <hl-alert type="info" title="消息提示的文案！"></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！"></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！"></hl-alert>
          `
        }
      },
      closable: {
        code: {
          html: `
          <hl-alert type="success" title="成功的提示文案！" closable></hl-alert>
        <hl-alert type="info" title="消息提示的文案！" closable></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！" closable></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！" closable></hl-alert>
          `
        }
      },
      closeText: {
        code: {
          html: `
          <hl-alert type="success" title="成功的提示文案！" closeText="我知道了"></hl-alert>
        <hl-alert type="info" title="消息提示的文案！" closeText="点击关闭"></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！" closable iconClass="hl hl-star-fill"></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！" closable iconClass="hl hl-github-fill"></hl-alert>
          `
        }
      },
      showIcon: {
        code: {
          html: `
          <hl-alert type="success" title="成功的提示文案！" closable showIcon></hl-alert>
        <hl-alert type="info" title="消息提示的文案！" closable showIcon></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！" closable center showIcon></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！" center showIcon></hl-alert>
          `
        }
      },
      effect: {
        code: {
          html: `
          <hl-alert type="success" title="成功的提示文案！" closable showIcon effect="dark"></hl-alert>
        <hl-alert type="info" title="消息提示的文案！" closable showIcon effect="dark"></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！" closable showIcon effect="dark"></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！" closable showIcon effect="dark"></hl-alert>
          `
        }
      },
      customize: {
        code: {
          html: `
          <hl-alert type="success" title="成功的提示文案！" closable center></hl-alert>
        <hl-alert type="info" title="消息提示的文案！" closable center></hl-alert>
        <hl-alert type="warning" title="警告提示的文案！" closable center></hl-alert>
        <hl-alert type="danger" title="危险提示的文案！" closable center></hl-alert>
          `
        }
      },
      alertApi: [
        {
          parameter: 'type',
          description: '选择主题',
          dataTypes: 'String',
          optional: 'success / warning / info / danger',
          default: 'info'
        },
        {
          parameter: 'title',
          description: '标题',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'closable',
          description: '是否可关闭',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'closeText',
          description: '自定义关闭按钮文字',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'iconClass',
          description: '自定义关闭按钮图标',
          dataTypes: 'String',
          optional: '自定义图标的地址，必须在closable模式下才起效',
          default: 'hl hl-close_1'
        },
        {
          parameter: 'center',
          description: '文字是否居中展示',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'showIcon',
          description: '是否展示图标',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'effect',
          description: '主题颜色',
          dataTypes: 'String',
          optional: 'light / dark',
          default: 'light'
        }
      ],
      EventsApi: [
        {
          parameter: 'close',
          description: '关闭alert时触发的事件',
          dataTypes: ''
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped></style>
