<template>
  <div class="content-box">
    <h2 class="demo-title">ZoomImg 图片逐渐放大</h2>
    <p class="demo-introduction">
      当鼠标移动到当前图片时，图片会逐渐的放大！此方法仅适用于图片展示，不包含任何事件！
    </p>

    <!-- 裁剪图片DEMO -->
    <hl-el-demo-block :height="700" :title="'图片放大'" id="upload-img-cropper">
      <template v-slot:demo>
        <hl-zoom-img :imgList="list"></hl-zoom-img>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(simple.code.html) }}</code>
        <code class="javascript">{{fCode(simple.code.javascript)}}</code>
      </template>
    </hl-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction">属性说明如下：</P>
      <hl-table-simple :data="api" :type="'demo'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'hl-upload-img-demo',
  data () {
    return {
      list: [
        {
          id: 1,
          src:
            'https://fuss10.elemecdn.com/d/e6/c4d93a3805b3ce3f323f7974e6f78jpeg.jpeg'
        },
        {
          id: 2,
          src:
            'https://fuss10.elemecdn.com/2/11/6535bcfb26e4c79b48ddde44f4b6fjpeg.jpeg'
        },
        {
          id: 3,
          src:
            'https://fuss10.elemecdn.com/3/28/bbf893f792f03a54408b3b7a7ebf0jpeg.jpeg'
        },
        {
          id: 4,
          src:
            'https://fuss10.elemecdn.com/1/34/19aa98b1fcb2781c4fba33d850549jpeg.jpeg'
        }
      ],
      simple: {
        code: {
          html: `
          <hl-zoom-img :imgList="list"></hl-zoom-img>
          `,
          javascript: `
          export default {
            data(){
              return{
                list: [
                  {
                    id: 1,
                    src:
                      'https://fuss10.elemecdn.com/d/e6/c4d93a3805b3ce3f323f7974e6f78jpeg.jpeg'
                  },
                  {
                    id: 2,
                    src:
                      'https://fuss10.elemecdn.com/2/11/6535bcfb26e4c79b48ddde44f4b6fjpeg.jpeg'
                  },
                  {
                    id: 3,
                    src:
                      'https://fuss10.elemecdn.com/3/28/bbf893f792f03a54408b3b7a7ebf0jpeg.jpeg'
                  },
                  {
                    id: 4,
                    src:
                      'https://fuss10.elemecdn.com/1/34/19aa98b1fcb2781c4fba33d850549jpeg.jpeg'
                  }
                ],
              }
            }
          }
          `
        }
      },
      api: [
        {
          parameter: 'imgList',
          description: '图片地址集合',
          dataTypes: 'Array',
          optional: '',
          default: ''
        },
        {
          parameter: 'row',
          description: '图片是否横向摆放，默认纵向',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'height',
          description: '图片的高度',
          dataTypes: 'Number',
          optional: '',
          default: '500'
        },
        {
          parameter: 'width',
          description: '图片的宽度',
          dataTypes: 'Number',
          optional: '',
          default: '780'
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped></style>
