<template>
  <transition name="hl-fade">
    <div class="hl-alert" :class="[center ? 'is-center' : '']" v-show="visible">
      <div
        class="hl-alert-box"
        :class="{
          'hl-alert-success': type === 'success',
          'hl-alert-info': type === 'info',
          'hl-alert-warning': type === 'warning',
          'hl-alert-danger': type === 'danger',
          'hl-alert-dark-success': effect === 'dark' && type === 'success',
          'hl-alert-dark-info': effect === 'dark' && type === 'info',
          'hl-alert-dark-warning': effect === 'dark' && type === 'warning',
          'hl-alert-dark-danger': effect === 'dark' && type === 'danger',
          'is-center': center
        }"
        v-if="title !== ''"
      >
        <!-- <i class="hl">知道了</i> -->
        <slot name="title">
          <i
            class="showIcon hl"
            :class="{
              'hl-check-circle-fill': type === 'success',
              'hl-info-circle-fill': type === 'info',
              'hl-warning-circle-fill': type === 'warning',
              'hl-close-circle-fill': type === 'danger'
            }"
            v-if="showIcon"
          ></i
          >{{ title }}
        </slot>
        <i
          class="icon-common"
          :class="iconClass !== '' ? iconClass : 'hl hl-close_1'"
          v-show="closable && closeText == ''"
          @click="close()"
        ></i>
        <i class="icon-common hl" v-show="closeText !== ''" @click="close()">{{
          closeText
        }}</i>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'hl-alert',
  props: {
    type: {
      type: String,
      default: 'info'
    },
    title: {
      type: String,
      default: ''
    },
    iconClass: {
      type: String,
      default: ''
    },
    effect: {
      type: String,
      default: 'light'
    },
    closable: {
      type: Boolean,
      default: false
    },
    showIcon: {
      type: Boolean,
      default: false
    },
    closeText: {
      type: String,
      default: ''
    },
    center: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return { visible: true }
  },
  mounted () {},
  methods: {
    close () {
      this.visible = false
      this.$emit('close')
    }
  }
}
</script>
<style lang="scss" scoped>
.hl-alert {
  margin-bottom: 20px;
  //   display: inline-block;
  .hl-alert-box {
    display: inline-block;
    position: relative;
  }
  .is-center {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .hl-alert-success {
    color: $--color-success;
    width: 100%;
    padding: 11px 15px;
    border-radius: 4px;
    font-size: 14px;
    background: $--color-success-light-7;
  }
  .hl-alert-info {
    color: $--color-info;
    width: 100%;
    font-size: 14px;
    padding: 11px 15px;
    border-radius: 4px;
    background: $--color-info-light-7;
  }
  .hl-alert-warning {
    color: $--color-warning;
    width: 100%;
    font-size: 14px;
    padding: 11px 15px;
    border-radius: 4px;
    background: $--color-warning-light-7;
  }
  .hl-alert-danger {
    color: $--color-danger;
    width: 100%;
    font-size: 14px;
    padding: 11px 15px;
    border-radius: 4px;
    background: $--color-danger-light-7;
  }
  .hl-alert-dark-success {
    color: $--color-white;
    width: 100%;
    font-size: 14px;
    padding: 11px 15px;
    border-radius: 4px;
    background: $--color-success;
    .icon-common {
      color: $--color-white!important;
    }
  }
  .hl-alert-dark-info {
    color: $--color-white;
    width: 100%;
    font-size: 14px;
    padding: 11px 15px;
    border-radius: 4px;
    background: $--color-info;
    .icon-common {
      color: $--color-white!important;
    }
  }
  .hl-alert-dark-warning {
    color: $--color-white;
    width: 100%;
    font-size: 14px;
    padding: 11px 15px;
    border-radius: 4px;
    background: $--color-warning;
    .icon-common {
      color: $--color-white!important;
    }
  }
  .hl-alert-dark-danger {
    color: $--color-white;
    width: 100%;
    font-size: 14px;
    padding: 11px 15px;
    border-radius: 4px;
    background: $--color-danger;
    .icon-common {
      color: $--color-white!important;
    }
  }
  .icon-common {
    position: absolute;
    color: #d4cdcc;
    top: 13px;
    right: 10px;
    font-size: 14px;
  }
  .showIcon {
    padding-right: 7px;
  }
  .icon-common:hover {
    cursor: pointer;
    color: #bcbcbc;
  }
  &::before {
    content: '';
  }
}
</style>
