<template>
  <div class="hl-box" :class="{ row: row }">
    <div
      class="hl-zoom-img"
      :style="'height:' + height + 'px;width:' + width + 'px'"
      v-for="(item, index) in imgList"
      :key="index"
    >
      <img :src="item.src"/>
    </div>
  </div>
</template>
<script>
export default {
  name: 'hl-zoom-img',
  props: {
    imgList: {
      type: Array,
      default: ''
    },
    row: {
      type: Boolean,
      default: false
    },
    height: {
      type: Number,
      default: 500
    },
    width: {
      type: Number,
      default: 800
    }
  },
  data () {
    return {}
  }
}
</script>
<style lang="scss" scoped>
.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.hl-box {
  height: 500px;
  overflow-y: scroll;
}
.hl-zoom-img {
  display: block;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
img {
  width: 100%;
  cursor: pointer;
  transition: all 4s;
}
img:hover {
  transform: scale(1.2); // 图片放大的倍数
}
</style>
