<template>
  <div class="content-box">
    <h2 class="demo-title">Card 卡片化</h2>
    <p class="demo-introduction">
      快速搭建卡片化结构，阴影默认使用浅色投影 box-shadow: 0 2px 12px 0 rgba(0,
      0, 0, 0.1)
    </p>
    <hl-anchor>
      <hl-anchor-link href="simple" title="card卡片化" />
      <hl-anchor-link href="title" title="有标题的卡片" />
      <hl-anchor-link href="border" title="标题有下划线的卡片" />
      <hl-anchor-link href="color" title="自定义的卡片" />
      <hl-anchor-link href="api" title="API" />
    </hl-anchor>
    <!-- card卡片化 -->
    <hl-el-demo-block
      :height="100"
      :title="'card卡片化'"
      id="simple"
    >
      <template v-slot:demo>
        <hl-card>我是卡片的内容</hl-card>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(simple.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- card卡片化,有标题 -->
    <hl-el-demo-block
      :height="100"
      :title="'有标题的卡片'"
      id="title"
    >
      <template v-slot:demo>
        <hl-card :title="'我是标题内容'">这是一个有标题的卡片</hl-card>
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(card.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- card卡片化,有标题，有下划线 -->
    <hl-el-demo-block
      :height="100"
      :title="'有下划线的标题卡片'"
      id="border"
    >
      <template v-slot:demo>
        <hl-card :title="'我是标题内容'" border
          >这是一个有下划线的标题卡片</hl-card
        >
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(border.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- card卡片化,有标题，有下划线,自定义字体颜色大小 -->
    <hl-el-demo-block
      :height="100"
      :title="'自定义标题的卡片'"
      id="color"
    >
      <template v-slot:demo>
        <hl-card
          :title="'我是标题内容'"
          border
          :color="'blue'"
          :titleColor="'red'"
          >这是一个自定义的标题卡片</hl-card
        >
      </template>
      <template v-slot:code>
        <code class="html">{{ fCode(color.code.html) }}</code>
      </template>
    </hl-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction">属性说明如下：</P>
      <hl-table-simple :data="api" :type="'demo'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'hl-card-demo',
  data () {
    return {
      simple: {
        code: {
          html: `
          <hl-card>我是卡片的内容</hl-card>
          `
        }
      },
      card: {
        code: {
          html: `
          <hl-card :title="'我是标题内容'">这是一个有标题的卡片</hl-card>
          `
        }
      },
      border: {
        code: {
          html: `
          <hl-card :title="'我是标题内容'" border>这是一个有下划线的标题卡片</hl-card>
          `
        }
      },
      color: {
        code: {
          html: `
          <hl-card :title="'我是标题内容'" border :color="'blue'" :titleColor="'red'">这是一个自定义的标题卡片</hl-card>
          `
        }
      },
      api: [
        {
          parameter: 'radius',
          description: '卡片的border-radius值',
          dataTypes: 'Number',
          optional: '0~50',
          default: '4'
        },
        {
          parameter: 'color',
          description: '卡片的文字颜色',
          dataTypes: 'String',
          optional: '',
          default: '#333'
        },
        {
          parameter: 'size',
          description: '卡片的文字大小',
          dataTypes: 'Number',
          optional: '',
          default: '14'
        },
        {
          parameter: 'title',
          description: '卡片的标题内容,不能为空',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'titleColor',
          description: '卡片的标题文字颜色,仅当title存在时生效',
          dataTypes: 'String',
          optional: '',
          default: '#bfbfbf'
        },
        {
          parameter: 'titleSize',
          description: '卡片的标题文字大小,仅当title存在时生效',
          dataTypes: 'Number',
          optional: '',
          default: '16'
        },
        {
          parameter: 'border',
          description: '卡片的是否有下划线分隔,仅当title存在时生效',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'align',
          description: '卡片的标题摆放位置',
          dataTypes: 'String',
          optional: 'left / center / right',
          default: 'left'
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped></style>
