<template>
  <div
    class="hl-card"
    :style="
      'border-radius:' + radius + 'px;color:' + color + ';font-size:' + size + 'px'
    "
  >
    <div
      class="hl-card-title"
      :class="{ border: border }"
      :style="'font-size:' + titleSize + 'px;color:' + titleColor"
      :align="align"
      v-if="title !== ''"
    >
      {{ title }}
    </div>
    <slot />
  </div>
</template>
<script>
export default {
  name: 'hl-card',
  props: {
    align: {
      type: String,
      default: 'left'
    },
    title: {
      type: String,
      default: ''
    },
    border: {
      type: Boolean,
      default: false
    },
    radius: {
      type: Number,
      default: 4
    },
    color: {
      type: String,
      default: '#333'
    },
    size: {
        type: Number,
        default: 14
    },
    titleColor: {
      type: String,
      default: '#bfbfbf'
    },
    titleSize: {
        type: Number,
        default: 16
    }
  },
  data () {
    return {}
  }
}
</script>
<style lang="scss" scoped>
.hl-card {
  width: 100%;
  padding: 15px;
  box-sizing: border-box;
  height: auto;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  .hl-card-title {
    //   border: 1px solid red;
    padding: 0 0 15px;
  }
  .border {
    border-bottom: 1px solid #f2f6fc;
    margin-bottom: 15px;
  }
}
</style>
