    <span class="hl-button-span" v-if="!circle">
      <slot />
    </span>
  </button>
</template>
<script>
export default {
  name: 'hl-button',
  props: {
    type: {
      type: String,
      default: 'default'
    },
    animation: {
      type: Boolean,
      default: true
    },
    transform: {
      type: Number,
      default: 0
    },
    disabled: {
      type: Boolean,
      default: false
    },
    circle: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      default: ''
    },
    size: {
      type: String,
      default: 'normal'
    },
    color: {
      type: String,
      default: ''
    },
    block: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      show: false,
      scrollTop: 0,
      customizeColor: {
        backgroundColor: this.color,
        borderColor: this.color
      }
    }
  },
  methods: {
    click () {
      this.$emit('click')
    }
  }
}
